import { isString, ShapeFlags } from "@vue/shared";
import { Text, createVnode, isSameVnode } from "./vnode";

export function createRenderer(renderOptions) {
  let {
    insert: hostInsert,
    remove: hostRemove,
    setElementText: hostSetElementText,
    setText: hostSetText,
    parentNode: hostParentNode,
    nextSibling: hostNextSibling,
    createElement: hostCreateElement,
    createText: hostCreateText,
    patchProp: hostPatchProp,
  } = renderOptions;

  /**
   * 如果新的虚拟dom的类型type是文本类型执行这个方法
   * 如果n1不存在 则是新增的逻辑，直接创建文本元素直接插入父级
   * 否则可以复用之前的元素的节点
   * @param n1
   * @param n2
   * @param container
   */
  const processText = (n1, n2, container) => {
    if (n1 === null) {
      hostInsert((n2.el = hostCreateText(n2.children)), container);
    } else {
      const el = (n2.el = n1.el);
      if (n1.children !== n2.children) {
        hostSetText(el, n2.children);
      }
    }
  };

  const normalize = (children, i) => {
    let child = children[i];
    if (isString(child)) {
      let vnode = createVnode(Text, null, child);
      children[i] = vnode;
    }
    return children[i];
  };

  const mountChildren = (children, container) => {
    for (let i = 0; i < children.length; i++) {
      //处理后要进行替换，否则children中存放的已经是字符串
      let child = normalize(children, i);
      patch(null, child, container);
    }
  };

  const mountElement = (vnode, container, anchor) => {
    let { type, props, children, shapeFlag } = vnode;
    let el = (vnode.el = hostCreateElement(type)); //将真实的元素挂载到这个虚拟节点上，后续用于复用节点和更新

    //创建元素后，给元素添加属性
    if (props) {
      for (let key in props) {
        hostPatchProp(el, key, null, props[key]);
      }
    }
    if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
      //文本
      hostSetElementText(el, children);
    } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
      //数组的话 需要再去挂载儿子
      mountChildren(children, el);
    }
    //创建完元素后，我们要把元素插入父节点
    hostInsert(el, container, anchor);
  };

  const patchProps = (oldProps, newProps, el) => {
    for (let key in newProps) {
      hostPatchProp(el, key, oldProps[key], newProps[key]);
    }

    for (let key in oldProps) {
      if (!newProps[key]) {
        hostPatchProp(el, key, oldProps[key], undefined);
      }
    }
  };

  const unmountChildren = (children) => {
    for(let i = 0; i < children.length; i++){
      unmount(children[i])
    }
  }
  //比较两个儿子的差异
  const patchKeyChildren = (c1, c2, el) => { 
    let i = 0
    let e1 = c1.length - 1
    let e2 = c2.length - 1

    //sync from start
    while (i <= e1 && i <= e2) {
      const n1 = c1[i]
      const n2 = c2[i]
      if (isSameVnode(n1, n2)) {
        patch(n1, n2, el) //这样做就是比较两个节点的属性和子节点
      }else{
        break
      }
      i++
    }

    //sync from end
    while (i <= e1 && i <= e2) {
      const n1 = c1[e1]
      const n2 = c2[e2]
      if (isSameVnode(n1, n2)) {
        patch(n1, n2, el)
      }else{
        break
      }
      e1--
      e2--
    }

    //common sequence + mount
    //i要比e1大说明有新增的
    //i 和 e2 之间是新增的部分

    //有一方全部比较完毕了，要么就删除，要么就添加
    if (i > e1) {
      if (i <= e2) {
        while (i <= e2) {
          const nextPos = e2 + 1
          //根据下一个人的索引来看参照物
          const anchor = nextPos < c2.length ? c2[nextPos].el : null
          patch(null, c2[i], el, anchor)
          i++
        }
      }
    // common sequence + unmount
    // i比e2大说明有要卸载的
    // i到e1之间的就是要卸载的
    }else if (i > e2) {
      if (i <= e1) {
        while ( i <= e1) {
          unmount(c1[i])
          i++
        }
      }
    }

    /**
     * 乱序对比
     */

    let s1 = i
    let s2 = i
    const keyToNewIndexMap = new Map()
    for(let i = s2; i < e2; i++){
      keyToNewIndexMap.set(c2[i].key, i) //记录新的虚拟节点 key 与索引的映射关系
    }

    //循环老的元素 看一下新的里面有没有 如果有说明要比较差异，没有要添加到列表中，老的有新的没有要删除
    
    const toBePatched = e2 - s2 + 1 //记录一下新的总数

    //记录是否比对过的映射表
    const newIndexToOldIndexMap = new Array(toBePatched).fill(0)

    for(let i = s1; i <= e1; i++){
      const oldChild = c1[i] //老的孩子
      // 用老的孩子去新的里面找
      let newIndex = keyToNewIndexMap.get(oldChild.key)

      if (newIndex === undefined) {
        unmount(oldChild)
      }else{
        //新的位置对应的老的位置，如果数组里放的值> 0 说明已经patch过了
        newIndexToOldIndexMap[newIndex-s2] = i+1
        patch(oldChild, c2[newIndex],el)
      }
    }
    //到了这一步是新老属性和儿子的对比，没有移动位置

    //需要移动位置
    for(let i = toBePatched - 1; i >= 0; i--){
      let index = i + s2 
      let current = c2[index] //找到对应的虚拟节点
      let anchor = index + 1 < c2.length ? c2[index + 1].el : null
      // if (newIndexToOldIndexMap[i] === 0) {
      if (!current.el) {

        patch(null, current, el, anchor)
      }else{
        hostInsert(current.el, el, anchor)
      }
    }
  }

  const patchChildren = (n1, n2, el) => {
    //比较两个虚拟节点的儿子的差异， el就是当前的父节点
    const c1 = n1.children
    const c2 = n2.children
    const prevShapeFlag = n1.shapeFlag //之前的
    const shapeFlag = n2.shapeFlag//之后的
    //文本 空的null 数组


    //比较两个儿子列表的差异
    if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
      if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        unmountChildren(c1)
      }
      if (c1 !== c2) {
        hostSetElementText(el,c2)
      }
    }else{

      if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
          patchKeyChildren(c1, c2, el)  //diff算法 全量比对
        }else{
          unmountChildren(c1)
        }
      }else{
        if (prevShapeFlag & ShapeFlags.TEXT_CHILDREN) {
          hostSetElementText(el, '')
        }
        if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
          mountChildren(c2,el)
        }
      }
    }

  };

  /**
   * 走到这一步代表两个节点是相同的，因为在patch中已经进行过判断了
   * @param n1
   * @param n2
   */
  const patchElement = (n1, n2) => {
    //先复用节点、在比较属性、在比较儿子
    let el = (n2.el = n1.el);
    let oldProps = n1.props || {};
    let newProps = n2.props || {};
    patchProps(oldProps, newProps, el);
    patchChildren(n1, n2, el);
  };
  const processElement = (n1, n2, container, anchor) => {
    if (!n1) {
      mountElement(n2, container, anchor);
    } else {
      //元素对比
      patchElement(n1, n2);
    }
  };
  /**
   *
   * 核心方法
   * @param n1 老的虚拟节点
   * @param n2 新的虚拟节点
   * @param container 父容器
   * @param anchor 插入元素的参照物
   */
  const patch = (n1, n2, container, anchor = null) => {
    if (n1 === n2) return;
    //判断两个元素是否相同，不相同卸载在添加
    if (n1 && !isSameVnode(n1, n2)) {
      unmount(n1);
      n1 = null;
    }
    const { type, shapeFlag } = n2;
    switch (type) {
      case Text:
        processText(n1, n2, container);
        break;
      default:
        if (shapeFlag & ShapeFlags.ELEMENT) {
          processElement(n1, n2, container, anchor);
        }
    }
  };
  const unmount = (vnode) => {
    hostRemove(vnode.el);
  };
  //vnode 虚拟dom
  const render = (vnode, container) => {
    //渲染过程是用你传入的renderOptions来渲染
    if (vnode == null) {
      //卸载逻辑
      if (container._vnode) {
        //之前确实渲染过了， 那么就卸载掉dom
        unmount(container._vnode);
      }
    } else {
      //这里既有初始化的逻辑，又有更新的逻辑
      patch(container._vnode || null, vnode, container);
    }
    container._vnode = vnode

  };

  return {
    render,
  };
}

/**
 * 更新的逻辑思考
 * 如果前后完全没关系， 删除老的 添加新的
 * 老的和新的一样，复用，属性可能不一样， 在对比属性，更新属性
 * 比儿子
 */
